/* עיצוב כללי */
.tasks-table-container {
    overflow-x: auto;
    padding: 1.5rem;
    background-color: #f0f2f5;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.tasks-table {
    width: 80%;
    border-collapse: collapse;
    border-spacing: 0;
    background-color: #ffffff;
    border-radius: 10px;
    overflow: hidden;
}

.tasks-table th,
.tasks-table td {
    padding: 1rem;
    text-align: right; /* יישור לימין עבור שפות RTL */
    border-bottom: 1px solid #e0e0e0;
}

/* עיצוב לכותרות */
.tasks-table th {
    background-color: #f4f6fa;
    font-weight: bold;
    color: #3a3f51;
    text-transform: uppercase;
    letter-spacing: 1px;
    font-size: 0.9rem;
    border-bottom: 2px solid #e0e0e0;
}

/* עיצוב שורות */
.tasks-table tr:nth-child(even) {
    background-color: #f9f9f9;
}

.tasks-table tr:hover {
    background-color: #eaf3ff;
    transition: background-color 0.2s ease-in-out;
}

.tasks-table td {
    color: #555555;
    font-size: 0.95rem;
}

/* עיצוב כפתורים */
.tasks-table button {
    margin-right: 0.5rem;
    padding: 0.5rem 1rem;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    font-size: 0.85rem;
    font-weight: bold;
    transition: background-color 0.2s ease, color 0.2s ease;
}

.tasks-table button:first-of-type {
    background-color: #28a745; /* כפתור שמירה */
    color: white;
}

.tasks-table button:last-of-type {
    background-color: #dc3545; /* כפתור ביטול */
    color: white;
}

.tasks-table button:not(:first-of-type):not(:last-of-type) {
    background-color: #007bff; /* כפתור עריכה */
    color: white;
}

/* אפקטים לכפתורים */
.tasks-table button:hover {
    opacity: 0.9;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.tasks-table button:first-of-type:hover {
    background-color: #218838;
}

.tasks-table button:last-of-type:hover {
    background-color: #c82333;
}

.tasks-table button:not(:first-of-type):not(:last-of-type):hover {
    background-color: #0069d9;
}

/* עיצוב רספונסיבי */
@media (max-width: 768px) {
    .tasks-table th,
    .tasks-table td {
        padding: 0.7rem;
        font-size: 0.85rem;
    }

    .tasks-table button {
        padding: 0.4rem 0.8rem;
        font-size: 0.8rem;
    }
}

@media (max-width: 480px) {
    .tasks-table th,
    .tasks-table td {
        padding: 0.5rem;
        font-size: 0.8rem;
    }

    .tasks-table button {
        padding: 0.3rem 0.6rem;
        font-size: 0.75rem;
    }
}

/* צבע לשורה כאשר המשימה בוצעה */
.completed-task {
    background-color: #d4edda !important; /* ירוק בהיר */
    color: #155724 !important;
}

/* צבע לשורה כאשר המשימה בתהליך */
.in-progress-task {
    background-color: #fff3cd !important; /* צהוב בהיר */
    color: #856404 !important;
}

/* צבע לשורה כאשר המשימה לא בוצעה */
.not-completed-task {
    background-color: #f8d7da !important; /* אדום בהיר */
    color: #721c24 !important;
}

  