/* עיצוב כללי לדף הוספת משימות */
.add-tasks-container {
    max-width: 600px;
    margin: 0 auto;
    padding: 1.5rem;
    border: 1px solid #ddd;
    border-radius: 8px;
    background-color: #f9f9f9;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    direction: rtl; /* יישור לימין */
    text-align: right; /* יישור טקסט לימין */
}

/* עיצוב לכותרת ותיאור */
.header-container {
    margin-bottom: 1.5rem;
}

/* עיצוב לכותרת */
.header {
    font-size: 2rem;
    font-weight: bold;
    color: #333;
    margin-bottom: 0.5rem;
}

/* עיצוב לתיאור */
.description {
    font-size: 1rem;
    color: #666;
    margin-bottom: 1rem;
}

/* עיצוב לניהול הקישור */
.tasks-link {
    display: inline-block;
    margin-top: 1rem;
    padding: 0.5rem 1rem;
    color: #007bff; /* צבע כחול לקישור */
    text-decoration: none;
    border-radius: 4px;
    background-color: #e7f0ff; /* רקע בהיר לקישור */
    transition: background-color 0.3s ease, color 0.3s ease;
}

/* שינוי צבע הקישור בעת ריחוף */
.tasks-link:hover {
    background-color: #d0e7ff;
    color: #0056b3; /* צבע כחול כהה יותר בעת ריחוף */
}

/* עיצוב נפרד לטופס */
.form-container {
    border: 1px solid #ddd;
    border-radius: 8px;
    background-color: #fff;
    padding: 1.5rem;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

/* עיצוב כללי לטופס */
form {
    margin: 0;
}

/* עיצוב לשדות של הטופס */
div {
    margin-bottom: 1rem;
}

/* עיצוב לתוויות */
label {
    display: block;
    font-weight: bold;
    margin-bottom: 0.5rem;
}

/* עיצוב לשדות טקסט */
input[type="text"],
textarea,
input[type="date"],
select {
    width: 100%;
    padding: 0.75rem;
    border: 1px solid #ccc;
    border-radius: 4px;
    box-sizing: border-box;
    font-size: 1rem;
    font-family: Arial, sans-serif;
}

/* עיצוב לאזורי טקסט */
textarea {
    height: 100px;
    resize: vertical;
}

/* עיצוב לכפתור השליחה */
button[type="submit"] {
    background-color: #4CAF50; /* צבע ירוק */
    color: white;
    border: none;
    border-radius: 4px;
    padding: 0.75rem 1.5rem;
    font-size: 1rem;
    cursor: pointer;
    transition: background-color 0.3s ease;
}

/* שינוי צבע הכפתור בעת ריחוף */
button[type="submit"]:hover {
    background-color: #45a049;
}
