/* עיצוב כללי לעמוד */
.login-container {
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #f4f4f9;
    padding: 1rem; /* הוספת padding כדי למנוע גלילה במסכים קטנים */
  }
  
  /* עיצוב לתיבה הכוללת */
  .login-wrapper {
    display: flex;
    background-color: white;
    border-radius: 8px;
    box-shadow: 0px 4px 15px rgba(0, 0, 0, 0.1);
    overflow: hidden;
    max-width: 1600px;
    width: 100%;
    height: 70vh; /* הגדרת גובה */
    flex-direction: column; /* ברירת מחדל למסכים קטנים */
  }
  
  /* עיצוב עבור התמונה */
  .login-image {
    width: 100%;
    height: 200px; /* גובה מוגדר */
  }
  
  .login-image img {
    width: 100%;
    height: 80%;
    object-fit: cover;
  }
  
  /* עיצוב כללי לטופס */
  .login-form {
    padding: 2rem;
    width: 80%;
  }
  
  .login-form h2 {
    font-size: 2rem;
    font-weight: bold;
    margin-bottom: 1.5rem;
    text-align: center;
    color: #333;
  }
  
  .login-form input {
    width: 100%;
    padding: 0.75rem;
    border: 1px solid #ddd;
    border-radius: 4px;
    margin-bottom: 1.5rem; /* שינוי לריווח אחיד */
    transition: border-color 0.3s ease;
    font-size: 1rem;
    box-sizing: border-box; /* הבטחה שה-padding לא ישפיע על הגודל */
  }
  
  .login-form input:focus {
    border-color: #3b71ca;
    outline: none;
  }
  
  .login-form .remember-me {
    display: flex;
    align-items: center;
    margin-bottom: 1.5rem;
  }
  
  .login-form .remember-me label {
    margin-left: 0.5rem;
    font-size: 0.875rem;
    color: #555;
  }
  
  /* עיצוב הכפתור */
  .login-form button {
    width: 100%;
    padding: 0.75rem;
    background-color: #3b71ca;
    color: white;
    font-size: 1rem;
    font-weight: bold;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    transition: background-color 0.3s ease;
  }
  
  .login-form button:hover {
    background-color: #2c5c9c;
  }
  
  /* עיצוב קישור לרישום */
  .login-form .register-link {
    text-align: center;
    margin-top: 1rem;
  }
  
  .login-form .register-link a {
    color: #3b71ca;
    text-decoration: none;
  }
  
  .login-form .register-link a:hover {
    text-decoration: underline;
  }
  
  /* עיצוב הודעת שגיאה */
  .error-message {
    color: #e74c3c;
    text-align: center;
    margin-top: 1rem;
  }
  
  /* רספונסיביות */
  @media (min-width: 768px) {
    .login-wrapper {
      flex-direction: row; /* במכשירים גדולים יותר הטופס והתמונה יהיו זה לצד זה */
    }
  
    .login-image {
      width: 50%; /* תמונה בצד אחד */
      height: auto;
    }
  
    .login-form {
      width: 50%; /* טופס בצד השני */
    }
  }
  
/* רספונסיביות */
@media (max-width: 480px) {
    .login-container {
      padding: 0.5rem; /* פחות padding במסכים קטנים מאוד */
    }
  
    .login-form h2 {
      font-size: 1.5rem;
    }
  
    .login-form input {
      padding: 0.5rem;
      font-size: 0.875rem;
    }
  
    .login-form button {
      padding: 0.5rem;
      font-size: 0.875rem;
    }
  
    .login-image {
      height: 120px; /* גובה נמוך יותר לתמונה במסכים קטנים */
      max-width: 80%; /* אפשרות להגדלת הרוחב */
      margin: 0 auto; /* מיקום התמונה במרכז */
      border-radius: 10px; /* עיגול פינות של התמונה */
      overflow: hidden; /* שמירה על הפרופורציות בתוך התיבה */
    }
  
    .login-image img {
      width: 100%;
      height: 100%;
      object-fit: cover; /* שמירה על כיסוי מלא של התמונה */
    }
  }