/* src/TasksPage.css */

/* הסבר רספונסיבי ומודרני */
.tasks-explanation {
    margin-bottom: 20px;
    background: linear-gradient(135deg, #e3f2fd, #90caf9);
    padding: 20px;
    border-radius: 12px;
    font-size: 1.1rem;
    line-height: 1.7;
    text-align: right;
    color: #333;
    box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1);
    transition: background 0.3s ease;
    max-width: 1510px;
  }
  
  .tasks-explanation:hover {
    background: linear-gradient(135deg, #bbdefb, #42a5f5);
  }
  
  .tasks-explanation p {
    margin-bottom: 1rem;
  }
  
  .tasks-explanation ul {
    list-style-type: disc;
    margin-left: 0;
    margin-right: 20px;
    padding-right: 20px;
    direction: rtl; /* מוודא שהכיווניות תהיה מימין לשמאל */
  }
  
  .tasks-explanation ul li {
    margin-bottom: 0.5rem;
    text-align: right; /* יישור הטקסט מימין */
  }
  
  .tasks-page h1 {
    text-align: center;
    color: #1565c0;
    font-size: 2rem;
    margin-bottom: 1.5rem;
  }
  
  /* עיצוב רספונסיבי לטבלה */
  .tasks-table-container {
    overflow-x: auto;
    margin: 0 auto;
    padding: 20px;
    max-width: 1200px;
    border-radius: 12px;
    background-color: #f8f9fa;
    box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.05);
  }
  
  .tasks-table {
    width: 100%;
    border-collapse: collapse;
    margin-top: 20px;
  }
  
  .tasks-table th,
  .tasks-table td {
    border: 1px solid #ddd;
    padding: 12px;
    text-align: right;
    color: #333;
  }
  
  .tasks-table th {
    background-color: #f1f1f1;
    font-weight: bold;
    color: #1565c0;
  }
  
  .tasks-table td {
    background-color: #fff;
    transition: background-color 0.3s ease;
  }
  
  .tasks-table tr:nth-child(even) td {
    background-color: #f9f9f9;
  }
  
  .tasks-table tr:hover td {
    background-color: #e3f2fd;
  }
  
  /* עיצוב לכפתורים */
  .tasks-table button {
    margin-right: 0.5rem;
    padding: 0.6rem 1rem;
    border: none;
    border-radius: 6px;
    cursor: pointer;
    font-size: 0.9rem;
    transition: background-color 0.3s ease;
  }
  
  .tasks-table button:first-of-type {
    background-color: #28a745; /* כפתור שמירה */
    color: white;
  }
  
  .tasks-table button:first-of-type:hover {
    background-color: #218838;
  }
  
  .tasks-table button:last-of-type {
    background-color: #dc3545; /* כפתור ביטול */
    color: white;
  }
  
  .tasks-table button:last-of-type:hover {
    background-color: #c82333;
  }
  
  .tasks-table button:not(:first-of-type):not(:last-of-type) {
    background-color: #007bff; /* כפתור עריכה */
    color: white;
  }
  
  .tasks-table button:not(:first-of-type):not(:last-of-type):hover {
    background-color: #0069d9;
  }
  
  /* עיצוב רספונסיבי */
  @media (max-width: 768px) {
    .tasks-page h1 {
      font-size: 1.6rem;
    }
  
    .tasks-explanation {
      font-size: 1rem;
      padding: 15px;
    }
  
    .tasks-table th,
    .tasks-table td {
      padding: 8px;
      font-size: 0.9rem;
    }
  
    .tasks-table button {
      padding: 0.4rem 0.8rem;
      font-size: 0.8rem;
    }
  }
  
  @media (max-width: 480px) {
    .tasks-page h1 {
      font-size: 1.4rem;
    }
  
    .tasks-explanation {
      font-size: 0.9rem;
      padding: 12px;
    }
  
    .tasks-table th,
    .tasks-table td {
      padding: 6px;
      font-size: 0.8rem;
    }
  
    .tasks-table button {
      padding: 0.3rem 0.6rem;
      font-size: 0.7rem;
    }
  }
  