/* General Table Styling */
.expenses-table-container {
  overflow-x: auto; /* מאפשר גלילה אופקית */
  padding: 1.5rem;
  background-color: #f0f2f5;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  width: 85%; /* תופס את כל רוחב המסך */
  margin: 0 auto; /* ממורכז */
  text-align: right;
}

.expenses-table {
  width: 100%;
  border-collapse: collapse;
  background-color: #ffffff;
  border-radius: 10px;
  overflow: hidden;
  min-width: 600px; /* מינימום רוחב לטבלה עבור מובייל */
  text-align: right;
}

.expenses-table th,
.expenses-table td {
  padding: 1rem;
  text-align: right; /* RTL alignment */
  border-bottom: 1px solid #e0e0e0;
  text-align: right;
}

.expenses-table th {
  background-color: #f4f6fa;
  font-weight: bold;
  color: #3a3f51;
  text-transform: uppercase;
  letter-spacing: 1px;
  font-size: 0.9rem;
  border-bottom: 2px solid #e0e0e0;
  text-align: right;
}

.expenses-table tr:nth-child(even) {
  background-color: #f9f9f9;
}

.expenses-table tr:hover {
  background-color: #eaf3ff;
  transition: background-color 0.2s ease-in-out;
}

.expenses-table td {
  color: #555555;
  font-size: 0.95rem;
}

.expenses-table button {
  margin-right: 0.5rem;
  padding: 0.5rem 1rem;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-size: 0.85rem;
  font-weight: bold;
  transition: background-color 0.2s ease, color 0.2s ease;
}

.expenses-table button:first-of-type {
  background-color: #28a745; /* Save button */
  color: white;
}

.expenses-table button:last-of-type {
  background-color: #dc3545; /* Cancel button */
  color: white;
}

.expenses-table button:hover {
  opacity: 0.9;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

/* עיצוב רספונסיבי */
@media (max-width: 768px) {
  .expenses-table-container {
    padding: 1rem;
  }

  .expenses-table th, .expenses-table td {
    font-size: 0.85rem;
    padding: 0.75rem;
  }

  .expenses-table {
    min-width: 100%; /* מבטיח שהתוכן לא ייחתך */
  }
}

@media (max-width: 480px) {
  .expenses-table-container {
    padding: 0.5rem;
  }

  .expenses-table th, .expenses-table td {
    font-size: 0.75rem;
    padding: 0.5rem;
  }

  .expenses-table {
    min-width: 100%; /* ממשיך להתאים למסכים קטנים יותר */
  }
}
