.App {
    display: flex;
    flex-direction: column;
    min-height: 100vh;
}

.App-header {
    background-color: #1a589b; /* צבע רקע כחול */
    color: white;
    padding: 1rem;
    text-align: center;
}

.HeaderLogo {
    font-size: 2.5rem; /* גודל טקסט גדול */
    font-weight: bold; /* טקסט מודגש */
    margin: 0; /* הסרת ריווח חיצוני */
    letter-spacing: 0.05em; /* ריווח בין אותיות */
    text-transform: uppercase; /* אותיות גדולות */
    font-family: 'Arial', sans-serif; /* משפחת פונט נקיה */
}
