/* עיצוב כללי לסיידבר */
.Sidebar {
    -webkit-font-smoothing: antialiased;
    text-size-adjust: 100%;
    --rem: 16;
    --google-font-color-materialiconstwotone: none;
    font-family: Roboto, Helvetica, Arial, sans-serif;
    font-size: 1.25rem;
    font-weight: 400;
    line-height: 1.625;
    letter-spacing: 0.00938em;
    white-space: nowrap;
    box-sizing: inherit;
    padding: 0px;
    color: rgba(0, 0, 0, 0.87); /* צבע טקסט כהה */
    overflow-y: auto;
    display: flex;
    flex-direction: column;
    z-index: 1200;
    position: fixed;
    top: 50px;
    height: calc(-2rem + 100vh);
    flex: 1 0 auto;
    outline: 0px;
    margin: 1rem;
    border-radius: 0.75rem;
    border-width: initial;
    border-style: none;
    border-color: initial;
    border-image: initial;
    box-shadow: rgba(0, 0, 0, 0.1) 0rem 1.25rem 1.6875rem 0rem; /* הצללה כהה */
    right: 0; /* מיקום בצד ימין */
    width: 250px; /* רוחב הסיידבר */
    transform: translateX(0px);
    transition: transform 0.3s ease; /* מעבר חלק */
    background: rgba(255, 255, 255, 0.9); /* צבע רקע לבן עם שקיפות */
}

.Sidebar ul {
    list-style-type: none; /* הסר את הנקודות */
    padding-left: 0; /* הסר ריווח פנימי */
    margin: 0; /* הסר ריווח חיצוני */
    display: flex;
    flex-direction: column;
    background: rgba(255, 255, 255, 0.9); /* צבע רקע בהיר עם שקיפות */
    align-items: center; /* מרכז את כל הפריטים */
}

.Sidebar li {
    margin-bottom: 10px; /* ריווח בין הכפתורים */
    width: 100%; /* רוחב מלא של אלמנטי הרשימה */
    display: flex;
    justify-content: center; /* מרכז את הכפתורים בתוך אלמנטי הרשימה */
    background: rgba(255, 255, 255, 0.9); /* צבע רקע בהיר עם שקיפות */
}

.Sidebar-button {
    font-family: 'Arial', sans-serif;
    font-size: 0.8rem;
    font-weight: bold;
    line-height: 1.5;
    letter-spacing: 0.05em;
    text-align: center;
    box-sizing: border-box;
    background: linear-gradient(135deg, #4a90e2, #357abd); /* גוונים כחולים */
    color: #ffffff;
    border: none;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 90%;
    padding: 0.75rem 1rem;
    margin: 10px 0;
    border-radius: 15px; /* פינות פחות מעוגלות */
    cursor: pointer;
    box-shadow: 0 4px 20px rgba(0, 0, 0, 0.2); /* הצללה בינונית */
    transition: all 0.4s ease; /* שינוי משך המעבר */
    text-transform: uppercase;
    text-decoration: none;
    position: relative; /* תכונה זו מאפשרת שימוש באפקטים פנימיים */
    overflow: hidden; /* שימוש באפקטים של מעבר צבע */
}

.Sidebar-button::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(255, 255, 255, 0.2); /* שכבה קלה של אור */
    opacity: 0;
    transition: opacity 0.4s ease;
    border-radius: inherit; /* שמירה על הפינות המעוגלות */
}

.Sidebar-button:hover::before {
    opacity: 1;
}

.Sidebar-button:active {
    transform: translateY(1px); /* הורדת הכפתור קלות בעת לחיצה */
    box-shadow: 0 4px 20px rgba(255, 255, 255, 0.3); /* צללה כמו במצב הרגיל */
}

/* כאשר הסיידבר פתוח במובייל */
.Sidebar.open {
    transform: translateX(0); /* מיקום הסיידבר כאשר הוא פתוח */
}

/* כאשר הסיידבר סגור במובייל */
.Sidebar.closed {
    transform: translateX(-100%); /* מחוץ למסך */
}

/* עיצוב כפתור toggle */
.Sidebar-toggle {
    position: fixed;
    top: 1rem;
    right: 1rem;
    background: #4a90e2; /* צבע רקע כחול */
    color: #ffffff; /* צבע טקסט לבן */
    border: none;
    border-radius: 50%;
    width: 50px;
    height: 50px;
    display: none; /* ברירת מחדל, מוסתר */
    align-items: center;
    justify-content: center;
    cursor: pointer;
    box-shadow: 0 4px 20px rgba(0, 0, 0, 0.2);
    font-size: 1.5rem;
    z-index: 1300; /* כדי להימנע מהכיסוי של הסיידבר */
}

/* עיצוב עבור מסכים קטנים יותר */
@media (max-width: 768px) {
    .Sidebar {
        width: 60%; /* רוחב הסיידבר במסכים קטנים */
        height: 100vh; /* גובה מלא של המסך */
        position: fixed; /* מיקום קבוע */
        margin: 0; /* מבטל את המרווח החיצוני */
        border-radius: 0; /* מבטל את העיגול בפינות */
        box-shadow: none; /* מבטל את הצללה */
        padding: 1rem; /* מוסיף ריווח פנימי */
        transform: translateX(100%); /* מחוץ למסך בצד ימין */
        transition: transform 0.3s ease; /* מעבר חלק */
    }

    .Sidebar.open {
        transform: translateX(0); /* מיקום הסיידבר כאשר הוא פתוח */
    }

    .Sidebar-toggle {
        display: flex; /* מראה את כפתור הסיידבר */
    }
}
